(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('WorkshopController', WorkshopController);

  /** @ngInject */
  function WorkshopController($sce, $routeParams, workshops, sidenav) {

    var vm = this;

    vm.trust = $sce.trustAsResourceUrl;
    vm.tag = sidenav.tag;

    function init() {
      workshops.one($routeParams.name).success(function (item) {
        vm.item = item;
      });
    }

    init();

  }
})();

(function () {
  'use strict';

  angular
    .module('frontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($routeParams, workshops, sidenav, menu) {
    var vm = this;

    vm.tag = $routeParams.tag;

    function init() {
      workshops.get().success(function (workshops) {
        vm.workshops = workshops;
      });
    }

    vm.menu = menu;
    vm.clear = sidenav.main;
    vm.openTag = sidenav.tag;
    vm.openWorkshop = sidenav.workshop;

    init();

  }
})();

